import ScatterJS from 'scatterjs-core';
import ScatterEOS from 'scatterjs-plugin-eosjs';
import Eos from 'eosjs';
ScatterJS.plugins(new ScatterEOS());
export let scatter = ScatterJS.scatter;

let privateKeyArray = ['5KFP9EyJ97VdwCXYSfa6QBXmEJNtdtv8nsdaifNTPWLtniEvyAW']; //合约所有人私钥
let chainId = "cf057bbfb72640471fd910bcb67639c22df9f92470936cddc1ade0e2f2e7dc4f";
let eosIp = "35.197.130.214";
let eosPort = "8888";
let protocol = "http";
const config = {
	keyProvider: privateKeyArray,
	// httpEndpoint: 'http://35.197.130.214:8888',
	httpEndpoint: protocol + '://' + eosIp + ':' + eosPort,
	chainId: chainId,
	expireInSeconds: 60,
	broadcast: true,
	debug: false,
	verbose: false,
	sign: true
};
const eos = Eos(config);

const network = {
	blockchain: 'eos',
	protocol: protocol,
	host: eosIp,
	port: parseInt(eosPort),
	chainId: chainId
};
let scatterEos = scatter.eos(network, Eos);
let currentAccount = null;

/**
 * 登录
 * @param {cd} cd 
 */
export function eoslogin(cd) {
	scatter.connect('app').then(connected => {
		if (!connected) return false;
		ScatterJS.scatter.getIdentity({
			accounts: [network]
		}).then((identity) => {
			currentAccount = identity.accounts[0]
			getinfo()
			return cd && cd(identity)
		}).catch(error => {
			console.log(error)
			alert(error + '')
		})
	});
}

/**
 * 注销
 */
export function eosclose() {
	ScatterJS.scatter.forgetIdentity();
}

/**
 * 获取账户信息
 * @param {cd} cd 
 */
export function getinfo(cd) {
	if (!currentAccount) {
		return false
	}
	eos.getAccount({
		account_name: currentAccount.name
	}).then((val) => {
		let info = {
			cpu: Math.round((val.cpu_limit.available / val.cpu_limit.max) * 100),
			net: Math.round((val.net_limit.available / val.net_limit.max) * 100)
		}
		return cd && cd(info)
	})
}

/**
 * 创建红包
 * @param userName 用户名(string)
 * @param amount 金额(number)
 * @param bomb 踩雷号码(number)
 * @param contractOwner 建立该合约的账户名(string)
 * @param gameName 游戏名称（string）
 * @param tokenName 代币合约所有人名称（string）
 * @param memo 备注(string)
 * @Result packetId 红包唯一标识
 * @Result txId 红包对应的区块
 */
export function createRedPacket(userName, amount, bomb, contractOwner, gameName, tokenName, memo) {
	if (typeof userName !== "string"
			|| typeof amount !== "number"
			|| typeof bomb !== "number"
			|| typeof contractOwner !== "string"
			|| typeof gameName !== "string"
			|| typeof tokenName !== "string"
			|| typeof memo !== "string")
			return false;

	if (userName === undefined
			|| amount === undefined
			|| bomb === undefined
			|| contractOwner === undefined
			|| gameName === undefined
			|| tokenName === undefined
			|| memo === undefined)
			return false;

	return new Promise(function(resolve, reject){
			scatter.connect(gameName).then(connected => {
					if(connected){
							// scatter.forgetIdentity();
							scatter.getIdentity({accounts: [network]}).then(result => {
									currentAccount = result.accounts[0];
									console.log(currentAccount.name + '登陆成功');
									scatterEos.transaction({
											actions: [
													{
															account: tokenName,
															name: 'transfer',
															authorization: [{
																	actor: currentAccount.name,
																	permission: currentAccount.authority
															}],
															data: {
																	from: currentAccount.name,
																	to: contractOwner,
																	quantity: amount + ".0000 EOS",
																	memo: memo
															}
													}
											]
									}).then(() => {
											console.log("转账成功");
											eos.getCurrencyBalance({
													code: "eosio.token",
													account: "wenbo",
													symbol: "EOS"
											}).then(result => console.log(result));
											eos.getCurrencyBalance({
													code: "eosio.token",
													account: "qizhan",
													symbol: "EOS"
											}).then(result => console.log(result));
											eos.contract(contractOwner).then(pickown => {
													pickown.createroom(userName, amount * 10000, bomb, {
															authorization: [contractOwner]
													}).then(result => {
															let consoleString = result.processed.action_traces[0].console;
															let response = {
																	"packetId": JSON.parse(consoleString).packet_id,
																	"txId": result.transaction_id
															};
															resolve(response);
													}).catch(e => {
															reject(e.message);
													});
											});
									}).catch(error => {
											reject(error.message);
									});
							}).catch(error => {
									reject(error.message);
							});
					}
			});
	});
}

/**
 * 抢红包
 * @param userName 用户名(string)
 * @param roomId 红包唯一标识(number)
 * @param contractOwner 建立该合约的账户名(string)
 * @param gameName 游戏名称（string）
 * @param tokenName 代币合约所有人名称（string）
 * @param transferAmount 转账金额（string）例：1.0000 EOS
 * @param memo 备注
 * @param referral 推荐渠道账户（string）(可选)
 * @Result packetId 抢到的红包唯一标识
 * @Result block_num 抢到的红包高对应的块信息
 * @Result packetAmount 抢到的金额
 * @Result isBomb 是否踩雷（1踩雷）
 * @Result isLast 是否最后一个红包（1是最后一个）
 * @Result isLuck 是否幸运号码
 * @Result luckyAmount 幸运奖金
 */
export function selectPacket(userName, roomId, contractOwner, gameName, tokenName, transferAmount, memo, referral) {
	if (typeof userName !== "string"
			|| typeof roomId !== "number"
			|| typeof contractOwner !== "string"
			|| typeof gameName !== "string"
			|| typeof tokenName !== "string"
			|| typeof transferAmount !== "string"
			|| typeof memo !== "string")
			return false;

	if (userName === undefined
			|| roomId === undefined
			|| contractOwner === undefined
			|| gameName === undefined
			|| tokenName === undefined
			|| transferAmount === undefined
			|| memo === undefined)
			return false;

	return new Promise(function (resolve, reject) {
			scatter.connect(gameName).then(connected => {
					if (connected) {
							// scatter.forgetIdentity();
							scatter.getIdentity({accounts: [network]}).then(result => {
									currentAccount = result.accounts[0];
									console.log(currentAccount.name + '登陆成功');
									scatterEos.transaction({
											actions: [
													{
															account: tokenName,
															name: 'transfer',
															authorization: [{
																	actor: currentAccount.name,
																	permission: currentAccount.authority
															}],
															data: {
																	from: currentAccount.name,
																	to: contractOwner,
																	quantity: transferAmount,
																	memo: memo
															}
													}
											]
									}).then(() => {
											console.log("转账成功");
											if(referral === undefined){
													eos.contract(contractOwner).then(pickown => {
															pickown.selpacket2(userName, roomId, {
																	authorization: [contractOwner]
															}).then(result => {
																	let consoleString = result.processed.action_traces[0].console;
																	consoleString = consoleString.substring(consoleString.indexOf("{"), consoleString.indexOf("}") + 1);
																	let response = {
																			"packetId": JSON.parse(consoleString).packet_id,
																			"block_num": result.processed.action_traces[0].block_num,
																			"packetAmount": JSON.parse(consoleString).packet_amount,
																			"isBomb": JSON.parse(consoleString).bomb,
																			"isLast": JSON.parse(consoleString).is_last,
																			"isLuck": JSON.parse(consoleString).luck,
																			"luckyAmount": JSON.parse(consoleString).prize_amount,
																	};
																	resolve(response);
															}).catch(e => {
																	reject(e.message);
															});
													});
											}else{
													eos.contract(contractOwner).then(pickown => {
															pickown.selectpacket(userName, roomId, referral, {
																	authorization: [contractOwner]
															}).then(result => {
																	let consoleString = result.processed.action_traces[0].console;
																	consoleString = consoleString.substring(consoleString.indexOf("{"), consoleString.indexOf("}") + 1);
																	let response = {
																			"packetId": JSON.parse(consoleString).packet_id,
																			"block_num": result.processed.action_traces[0].block_num,
																			"packetAmount": JSON.parse(consoleString).packet_amount,
																			"isBomb": JSON.parse(consoleString).bomb,
																			"isLast": JSON.parse(consoleString).is_last,
																			"isLuck": JSON.parse(consoleString).luck,
																			"luckyAmount": JSON.parse(consoleString).prize_amount,
																	};
																	resolve(response);
															}).catch(e => {
																	reject(e.message);
															});
													});
											}
									}).catch(error => {
											reject(error.message);
									});
							}).catch(error => {
									reject(error.message);
							});
					}
			});
	});
}

/**
 * 发红包榜单
 * @param contractOwner 建立该合约的账户名(string)
 * @resule JSON数组
 */
export function redPacketList(contractOwner){
	if(typeof contractOwner !== "string" || contractOwner === undefined) return false;
	return new Promise(function(resolve, reject) {
			eos.contract(contractOwner).then(pickown => {
					pickown.printboard({
							authorization: [contractOwner]
					}).then(result => {
							var list = result.processed.action_traces[0].console;
									list = replaceAll("[", "{", list);
									list = replaceAll("]", "}", list);
									list = "[" + list.substring(1, list.length - 4) + "]";
									let array = JSON.parse(list);
									resolve(array);
					}).catch(e => {
							console.error(e);
							resolve(false);
					});
			});
	})
}

function replaceAll(a, b, str) {
	while(str.indexOf(a) !== -1){
			str = str.replace(a, b);
	}
	return str;
}

/**
 * 奖金池
 * @Result 返回奖金数量(eg:16910 = 1.6910 EOS)
 */
export function prizePool(contractOwner){
	if(typeof contractOwner !== "string" || contractOwner === undefined) return false;
	return new Promise(function(resolve, reject) {
			eos.contract(contractOwner).then(pickown => {
					pickown.printpool({
							authorization: [contractOwner]
					}).then(result => {
							resolve(result.processed.action_traces[0].console);
					}).catch(e => {
							reject(e.message);
					});
			});
	})
}


